const mutations = {
    setLogin(state, value) {
        state.loggedIn = value;
    },
    setTitle(state, value) {
        state.title = value;
    },
    setBack(state, value) {
        state.back = value;
    },
    setLevelNumber(state, value) {
        state.levelNumber = value;
    },
    setLevelName(state, value) {
        state.levelName = value;
    },
    setTime(state, value) {
        state.time = value;
    },
    setSound(state, value) {
        state.sound = value;
    },
    setSounds(state, value) {
        state.sounds = value;
    },
    setPlayAudio(state, value) {
        state.playAudio = value;
    }
};
export default mutations;
