const getters = {
    getLogin(state) {
        const { loggedIn } = state;
        return loggedIn;
    },
    getTitle(state) {
        const { title } = state;
        return title;
    },
    getBack(state) {
        const { back } = state;
        return back;
    },
    getLevelNumber(state) {
        const { levelNumber } = state;
        return levelNumber;
    },
    getLevelName(state) {
        const { levelName } = state;
        return levelName;
    },
    getTime(state) {
        const { time } = state;
        return time;
    },
    getSound(state) {
        const { sound } = state;
        return sound;
    },
    getSounds(state) {
        const { sounds } = state;
        return sounds;
    },
    playAudio(state) {
        const { playAudio } = state;
        return playAudio;
    }
};
export default getters;
